header{
    background-color: var(--background-header);
    border-top: 3px solid var(--color-primary-main);
}

.header {
    display: flex;
    align-items: center;
    max-width: 1100px;
    margin: auto;
    position: relative;
    text-align: left;
    justify-content: space-between;
    box-sizing: border-box;

    /* Dropdown Button */
    .avatar-dropdown {
        height: 32px;

        .relative {
            border: none;
            box-shadow: none;
        }

        button {
            cursor: pointer;
            border: none;
            box-shadow: none;

            img {
                border: none;
                box-shadow: none;
            }
        }

        .menu {
            position: absolute;
            right: 0;
            margin-top: .5rem;
            font-size: 13px;
            line-height: 18px;
            min-width: 160px;
            text-align: left;
            width: 100%;
            list-style: none;
            cursor: pointer;
            transition: opacity 100ms linear, transform 100ms linear, visibility 100ms linear;
            border-radius: 6px;
            box-shadow: 0 5px 26px 0 rgba(0,0,0,.32);
            color: #29303b;
            background-color: rgb(39, 39, 39);
            border: 1px solid var(--color-border);
            z-index: 11;

            li {
                color: rgb(214,214,214);

                i {
                    font-size: 20px;
                    margin: 0 10px;
                    color: rgb(214,214,214);
                }

                &:hover {
                    background: rgb(54,54,54);
                }

                a {
                    display: block;
                    padding: 7px 15px;
                    width: 100%;
                    color: inherit;
                }
            }
        }
    }

    .form-search {
        flex: 1 1 0;
        display: none;
        font-size: 14px;
        box-sizing: border-box;
        padding: 0 12px;
        align-items: center;
        position: relative;
        margin-left: auto;
        width: 100%;

        .header_search {
            width: 100%;
            margin-left: auto;

            .search {
                display: block;
                font-size: 13px;
                padding: 8px 8px 8px 32px;
                border-radius: 3px;
                width: 100%;
                height: auto;

                &:focus {
                    width: 100%;
                }

                &:focus-within #navbar-links {
                    display: none;
                }
            }

            .mdi-magnify {
                left: 22px;
                position: absolute;
                top: 50%;
                font-size: 18px;
                z-index: 3;
                -ms-transform: translateY(-50%);
                transform: translateY(-50%);
            }

            .mdi-file-excel-box {
                cursor: pointer;
                right: 22px;
                position: absolute;
                top: 50%;
                font-size: 18px;
                z-index: 3;
                -ms-transform: translateY(-50%);
                transform: translateY(-50%);

                &:hover {
                    //background: rgba(167, 182, 194, .3);
                    text-decoration: none;
                    color: #182026;
                    border-radius: 3px;
                }
            }
        }
    }

    .logo {
        display: inline-block;
        vertical-align: middle;
        position: relative;
        color: var(--text-white-secondary);
        transition-duration: 0.2s;
        font-family: MontDemo-Heavy, serif;
        font-size: 26px;
        border-right: 1px solid #d5dddf;
        padding-right: 18px;

        &:hover {
            text-decoration: none;
            color: #fff;
        }
    }

    .main-navbar__section {
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        position: relative;
        min-height: 45px;

        .mdi-menu {
            display: none;
        }

        .mdi-magnify, .mdi-bell, .mdi-email {
            cursor: pointer;
            opacity: .7;
            font-weight: 500;
            font-size: 1.2rem;
            color: var(--text-white-secondary);

            &:hover {
                opacity: 1;
            }
        }

        .nav-links {
            display: flex;
            margin: 0;
            padding: 0 0 0 18px;
            list-style: none;

            .nav-links__item {
                align-items: center;
                margin-right: 23px;

                .nav-links__item-link {
                    color: rgb(214, 214, 214);
                    font-size: 13px;
                    line-height: 44px;
                    -webkit-transition: 0.2s;
                    transition: 0.2s;
                    -webkit-transition-property: all;
                    transition-property: all;
                    -webkit-transition-duration: 0.2s;
                    transition-duration: 0.2s;
                    -webkit-transition-timing-function: ease;
                    transition-timing-function: ease;
                    -webkit-transition-delay: 0s;
                    transition-delay: 0s;

                    &:hover {
                        background: unset;
                        color: var(--color-primary-main);
                    }
                }
            }
        }
    }
}

@media (max-width: 1281px) {
    .main-navbar {
        padding: 0 16px;
        width: unset !important;
    }

    .logo {
        border-right: unset !important;
    }

    .mdi-menu {
        display: block !important;
        color: var(--text-black-primary);
        width: 26px;
        font-size: 26px;
        margin-right: 18px;
    }

    .main-navbar {
        height: 48px;
    }

    .nav-links {
        display: none !important;
    }

    .nav-links .active {
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        padding: 0;
        overflow: hidden;
        background: #212121;
        width: 250px;
        box-sizing: border-box;
        transition: all 250ms;
        -webkit-transform: translateZ(0) translateX(-100%);
        transform: translateZ(0) translateX(-100%);
        text-align: center;
        box-shadow: 0 0 10px #000;
    }
}

.nav-links__item-link_current {
    color: var(--color-primary-main) !important;
}
