:root {
  --phone: 480px;
  --tablet: 768px;
  --desktop: 980px;
  --widescreen: 1200px;
  --font-sans-serif: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  --font-serif: "Times New Roman", Times, Baskerville, Georgia, serif;
  --base-background-color: #f6f6fb;
  --base-footer-color: #223645;
  --base-font-family: var(--font-sans-serif);
  --base-font-size: 16px;
  --base-line-height: 1.2;
  --base-text-color: var(--color-black);
  --base-min-width: 320px;
  --base-link: var(--color-blue);
  --base-link-active: var(--color-red);
  --btn-color: var(--color-white);
  --btn-background: var(--color-blue);
  --btn-padding: 0.4em 0.7em;
  --btn-font-size: 1.6rem;
  --btn-font-family: var(--font-sans-serif);
  --form-font-family: var(--font-sans-serif);
  --form-element-padding: var(--btn-padding);
  --form-element-border-color: var(--color-grey);
  --form-element-focus-border-color: var(--color-black);
  --placeholder-color: var(--color-grey);
  --headings-font-family: var(--font-sans-serif);
  --headings-color: var(--base-text-color);
  --headings-sizes-h1: 50px;
  --headings-sizes-h2: 45px;
  --headings-sizes-h3: 35px;
  --headings-sizes-h4: 25px;
  --headings-sizes-h5: 18px;
  --headings-sizes-h6: 16px;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html, body {
  display: flex;
  width: 100%;
  background-color: var(--background-default);
  font: var(--base-font-size)/var(--base-line-height) var(--base-font-family);
  min-width: var(--base-min-width);
  flex-direction: column;
}

body {
  color: var(--text-black-primary);
  min-height: 100vh;
}

th {
  text-align: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

ul, menu, dir, p {
  display: block;
  list-style-type: none;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
  -webkit-margin-start: 0;
  -webkit-margin-end: 0;
  -webkit-padding-start: 0;
  -moz-padding-start: 0;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6,
.h {
  font-family: var(--headings-font-family);
  font-weight: bold;
  font-style: normal;
  margin: 0 0 0.5em;
  text-transform: none;
}

h1, .h1 {
  font-size: var(--headings-sizes-h1);
}

h2, .h2 {
  font-size: var(--headings-sizes-h2);
}

h3, .h3 {
  font-size: var(--headings-sizes-h3);
}

h4, .h4 {
  font-size: var(--headings-sizes-h4);
}

h5, .h5 {
  font-size: var(--headings-sizes-h5);
}

h6, .h6 {
  font-size: var(--headings-sizes-h6);
}

p {
  font-weight: 400;
  font-size: 15px;
  line-height: 1.7;
  font-style: normal;
}

a {
  color: var(--base-link);
  text-decoration: none;
}
a:focus {
  text-decoration: none;
}

.link__grey {
  color: var(--color-grey-dark);
}

form,
fieldset {
  margin: 0;
  padding: 0;
  border-style: none;
}

input[type=text],
input[type=tel],
input[type=email],
input[type=search],
input[type=password],
textarea {
  max-width: 100%;
  width: 100%;
  background: var(--background-paper);
  padding: 9px 12px;
  border: 1px solid rgba(19, 28, 33, 0.1);
  color: var(--text-black-secondary);
  transition: 0.2s ease-in-out;
  transition-property: color, background-color, border;
  height: 40px;
  vertical-align: middle;
  display: inline-block;
}
input[type=text]:focus,
input[type=tel]:focus,
input[type=email]:focus,
input[type=search]:focus,
input[type=password]:focus,
textarea:focus {
  outline: none;
  border-color: var(--color-primary-light);
}

select {
  border-radius: 0;
}

textarea {
  resize: vertical;
  vertical-align: top;
}

button,
input[type=button],
input[type=reset],
input[type=file],
input[type=submit] {
  appearance: none;
  border-radius: 0;
  cursor: pointer;
  font-family: var(--form-font-family);
}

:root, [data-theme=default] {
  --background-default: #edeef0;
  --background-paper: #ffffff;
  --background-tertiary: #fafbfc;
