  --text-white-primary: #fff;
  --text-white-secondary: rgba(255, 255, 255, 0.7);
  --text-white-disabled: rgba(255, 255, 255, 0.5);
  --color-primary-main: #3d84a8;
  --color-primary-main-button: #3d84a8;
  --color-primary-light: #4791db;
