.loading {
  position: relative;
  background-color: #E2E2E2;
}
.loading::after {
  display: block;
  content: "";
  position: absolute;
  width: 50%;
  height: 100%;
  transform: translateX(-100%);
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  animation: loading 1.5s infinite;
}

@keyframes loading {
  100% {
    transform: translateX(100%);
  }
}
.tooltip {
  display: inline;
  perspective: 500px;
}
.tooltip:hover {
  cursor: pointer;
}
.tooltip:hover .tooltip__content {
  transform: translate3d(-50%, -10%, 0);
  opacity: 1;
  pointer-events: auto;
}
.tooltip__content {
  transition: all 200ms ease;
  transform: translate3d(-50%, 0%, 0);
  transform-origin: 0 10px;
  background-color: #fff;
  border-radius: 4px;
  color: #2f2f2f;
  display: block;
  font-size: 14px;
  font-weight: normal;
  left: 50%;
  opacity: 0;
  padding: 10px 20px;
  pointer-events: none;
  position: absolute;
  text-align: left;
  bottom: 100%;
}
.tooltip__content:before {
  border-color: transparent;
  border-top-color: var(--text-white-primary);
  border-style: solid;
  border-width: 10px;
  content: " ";
  display: block;
  height: 0;
  left: 50%;
  margin-left: -10px;
  position: absolute;
  bottom: -20px;
  width: 0;
}
.tooltip__content:after {
  content: " ";
  display: block;
  position: absolute;
  bottom: -20px;
  left: 0;
  width: 100%;
  height: 20px;
}
.tooltip__content:hover {
  transform: rotateX(0deg) translate3d(-50%, -10%, 0);
  opacity: 1;
  pointer-events: auto;
}

.icon-spinner {
  display: inline-block;
  font-size: 0.8em;
