  content: "";
  z-index: 1;
  position: absolute;
  animation: MuiSkeleton-keyframes-wave 1.6s linear 0.5s infinite;
  transform: translateX(-100%);
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.6), transparent);
