}
.card .card-body .text-center {
  text-align: center;
}
.card .card-footer {
  font-weight: 400;
  box-sizing: border-box;
  border-radius: 0 0 calc(.25rem - 1px) calc(.25rem - 1px);
  border-top: 1px solid var(--color-border);
  background: 0 0;
  padding: 20px 25px;
}
.card .card-footer .row {
  display: grid;
  grid-auto-flow: column;
  text-align: center;
}
.card .card-footer .row .col {
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
  border-right: 1px solid var(--color-border);
}
.card .card-footer .row .col:last-child {
  border-right: unset;
}

.scroll-top {
  background-color: #272727;
  position: fixed;
  width: 60px;
  height: 100%;
  z-index: 4;
  cursor: pointer;
}
.scroll-top .mdi-chevron-up {
  display: block;
  opacity: 1;
  width: 60px;
  text-align: center;
  color: white;
  font-size: 32px;
}
.scroll-top:hover {
  opacity: 0.7;
  transition: opacity 0.2s 0s, visibility 0s 0s;
}